.problem__cart {
	margin: 60px 0;
	color: #333; }
.cart {
	width: 100%;
	justify-content: space-around;
	&__item {
		width: 33%;
		min-height: 500px;
		text-align: center;
		overflow: hidden;
		position: relative;
		&:first-child {
			border-top: 10px solid #e8e8e8; }
		&:last-child {
			border-top: 10px solid #e8e8e8; }
		&-box {
			position: relative;
			width: 100%;
			height: 440px;
			background-repeat: no-repeat;
			background-size: cover;
			background-position: center;
			&-center {
				&:before {
					content: '';
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					background-color: #e0e633;
					opacity: .1; } } }
		&:after {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			transform: translateY(-100%);
			background-color: $accent;
			transition: transform .25s ease; }
		&:hover {
			color: #fff;
			border-top: 10px solid $accent;
			&:after {
				transform: translateY(0); } } }
	&__img {
		display: inline-block;
		position: absolute;
		z-index: 2;
		@include vcenter;
		width: 110px;
		height: 110px;
		border: 1px solid #dcdcdc;
		border-radius: 50%;
		background-repeat: no-repeat;
		background-size: 50px;
		background-position: center; }
	h4 {
		margin: 50px 0;
		text-transform: uppercase;
		font-size: 18px;
		position: relative;
		z-index: 2;
		span {
			font-family: rl; } } }
.fact {
	padding: 40px 0 60px 0;
	width: 100%;
	text-align: center;
	color: #fff;
	background-repeat: no-repeat;
	background-origin: center;
	background-size: cover;
	h3 {
		margin-bottom: 20px;
		text-transform: uppercase;
		letter-spacing: 4px;
		font-size: 26px; }
	p {
		font-family: rl;
		font-size: 24px; } }
.method__icons {
	margin-top: 40px; }
.listimg {
	width: 100%;
	padding: 75px 0 30px 0;
	flex-wrap: nowrap;
	align-items: flex-start;
	color: #333;
	&__left {
		min-width: 440px;
		margin-right: 80px;
		border-bottom: 5px solid #e8e8e8;
		a {
			display: inline-block;
			margin-bottom: 20px;
			width: 100%;
			height: 200px;
			position: relative;
			background-repeat: no-repeat;
			background-size: cover;
			background-position: center;
			cursor: pointer;
			color: #333;
			filter: saturate(0);
			transition: all .25s ease;
			&:after {
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background-color: #fff;
				opacity: .4;
				transition: all .25s ease; }
			&:first-child {
				background-position: left -70px;
				background-size: 160%; }
			&:focus {
				color: #fff;
				filter: saturate(1);
				&:after {
					background-color: #000;
					opacity: .4; } }
			&:hover {
				&:after {
					box-shadow: inset 0px 0px 30px 20px rgba(#000, .7); } }
			h4 {
				margin: 70px 0 0 20px;
				text-transform: uppercase;
				font-size: 26px;
				position: relative;
				z-index: 1; } } }
	&__right {
		width: 100%;
		h3 {
			font-size: 30px;
			text-transform: uppercase;
			line-height: 1.3;
			margin-bottom: 30px; }
		p {
			font-size: 25px;
			color: $accent;
			margin-bottom: 30px; }
		&-item {
			li {
				&:before {
					top: 2px;
					left: 0;
					width: 18px;
					height: 18px;
					border: 3px solid $icon;
					border-radius: 50%; } } } } }
.list__active {
	color: #fff !important;
	filter: saturate(1) !important;
	&:after {
		background-color: #000 !important;
		opacity: .4 !important; }
	&:before {
		content: '';
		position: absolute;
		height: 0;
		width: 0;
		top: 30px;
		right: -60px;
		border-top: 70px solid transparent;
		border-left: 50px solid #e8e8e8;
		border-bottom: 70px solid transparent; } }
.listimg__slider {
	width: 100%;
	overflow-x: hidden;
 }	// TECHNO
.techno-wrapper {
	background-color: #e8e8e8;
	margin-top: 70px; }
.techno {
	p {
		font-size: 18px;
		color: $grey;
		font-family: rl; } }
.stage__floor {
	position: absolute;
	left: 0;
	top: -22px;
	li {
		a {
			transition: background-color .25s ease, color .25s ease;
			position: relative;
			z-index: 2;
			font-family: bold;
			color: #333;
			text-transform: uppercase;
			display: inline-block;
			border: 2px solid #e8e8e8;
			padding: 10px 50px;
			border-left: none; }
		&:first-child > a {
			border-left: 2px solid #e8e8e8; } }
	&-active {
		background-color: $accent;
		position: relative;
		color: #fff !important;
		&:before {
			content: '';
			position: absolute;
			height: 0;
			width: 0;
			bottom: -20px;
			left: 50%;
			margin-left: -25px;
			border-left: 25px solid transparent;
			border-right: 25px solid transparent;
			border-top: 17px solid $accent; } } }
.stage__slide {
	flex-wrap: nowrap;
	padding: 60px 0 30px 0;
	&-img {
		width: 50%;
		margin: 15px; }
	&-right {
		width: 50%;
		margin: 15px 15px 15px 30px;
		h3 {
			font-family: bold;
			color: #333;
			text-transform: uppercase;
			font-size: 32px; }
		&-list {
			margin-top: 30px;
			li {
				&:before {
					width: 20px;
					height: 20px;
					background-image: url(../img/svgo/tick-box.svg);
					background-size: 20px; } } } } }
.stage__nav {
	position: absolute;
	display: inline-block;
	width: 80px;
	height: 67px;
	font-size: 35px;
	line-height: 67px;
	bottom: 50px;
	color: #757575;
	left: 52%;
	background-color: #dfdfdf;
	transition: background-color .25s ease, color .25s ease;
	&:hover {
		background-color: $accent;
		color: #fff; }
	&-next {
		left: calc(52% + 80px); }
	&-text {
		position: absolute;
		height: 67px;
		width: 120px;
		padding-left: 10px;
		font-size: 18px;
		font-family: bold;
		line-height: 73px;
		left: calc(52% + 160px);
		bottom: 50px;
		color: #333;
		text-transform: uppercase;
		background-color: #dfdfdf; } }
.profit__content {
	width: 100%;
	padding: 80px 0 40px 0;
	justify-content: space-between;
	&-left,
	&-right {
		width: 300px;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		min-height: 350px;
		span {
			position: relative;
			width: 100%;
			display: inline-block;
			background-color: #e8e8e8;
			text-align: right;
			padding: 20px 30px 20px 60px;
			color: #333;
			&:before {
				content: '';
				position: absolute;
				height: 60px;
				width: 60px;
				right: -110px;
				top: 20%;
				border: 2px solid #babfbf;
				border-radius: 50%;
				background-repeat: no-repeat;
				background-position: center;
				background-size: 30px; } }
		h4 {
			text-transform: uppercase;
			font-size: 14px;
			margin-bottom: 5px; }
		p {
			font-family: rl;
			font-size: 14px; }
		span:nth-child(1) {
			&:before {
				background-image: url(../img/svgo/piggy-bank-blue.svg); } }
		span:nth-child(2) {
			&:before {
				background-image: url(../img/svgo/garbage-blue.svg); } }
		span:nth-child(3) {
			&:before {
				background-image: url(../img/svgo/time-blue.svg); } } }
	&-right {
		span {
			text-align: left;
			padding: 20px 10px 20px 30px;
			&:before {
				left: -110px; } }
		span:nth-child(1) {
			&:before {
				background-image: url(../img/svgo/clean.svg); } }
		span:nth-child(2) {
			&:before {
				background-image: url(../img/svgo/glass-blue.svg); } }
		span:nth-child(3) {
			&:before {
				background-image: url(../img/svgo/forest-blue.svg); } } }
	&-center {
		flex-grow: 1;
		span {
			display: inline-block;
			width: 100%;
			height: 100%;
			background-repeat: no-repeat;
			background-size: center 100%;
			background-position: center; } } }
.safe {
	p {
		font-family: rl;
		color: $grey; }
	&__content {
		padding: 40px 0;
		justify-content: space-between;
		&-item {
			padding-bottom: 80px;
			position: relative;
			width: 49%;
			> span {
				display: inline-block;
				position: relative;
				width: 100%;
				height: 280px;
				background-repeat: no-repeat;
				background-size: cover;
				background-position: center;
				&:before {
					content: '';
					position: absolute;
					height: 10px;
					width: 100%;
					background-color: #e8e8e8;
					left: 0;
					bottom: -25px; } }
			.list__item {
				margin-top: 70px;
				span {
					border-color: #b0b6b6;
					&:before {
						content: '';
						bottom: -155px;
						position: absolute;
						left: 50%;
						margin-left: -3px;
						background-color: #b0b6b6;
						height: 6px;
						width: 6px;
						border-radius: 50%; }
					&:after {
						content: '';
						bottom: -149px;
						position: absolute;
						left: 50%;
						margin-left: -.5px;
						background-color: #b0b6b6;
						height: 135px;
						width: 1px;
						border-radius: 50%; } }

				h4 {
					color: #333;
					font-size: 16px;
					a {
						color: #333;
						text-transform: uppercase;
						text-decoration: underline #b0b6b6;
						&:hover {
							color: $icon;
							text-decoration: underline $icon; } } }
				p {
					font-size: 16px;
					text-transform: uppercase;
					padding-left: 0; } }
			p {
				padding: 0 60px 0 75px; }
			button {
				position: absolute;
				left: 75px;
				bottom: 0; } } } }
.partner {
	p {
		font-family: rl; }
	&__content {
		margin-top: 50px; }
	&__form {
		width: 35%;
		position: relative;
		padding: 40px 65px;
		margin-right: 90px;
		background-color: $foot;
		button {
			width: 100%; }
		&-icon {
			width: 90px;
			position: absolute;
			height: 100%;
			top: 0;
			right: -90px;
			background-color: #e8e8e8;
			span {
				display: inline-block;
				position: relative;
				width: 60px;
				height: 60px;
				margin: 20px 15px;
				background-repeat: no-repeat;
				background-size: 40px;
				background-position: center;
				&:before {
					content: '';
					bottom: -20px;
					position: absolute;
					left: 50%;
					margin-left: -3px;
					background-color: #b0b6b6;
					height: 6px;
					width: 6px;
					border-radius: 50%; }
				&:after {
					content: '';
					bottom: -480px;
					position: absolute;
					left: 50%;
					margin-left: -.5px;
					background-color: #b0b6b6;
					height: 460px;
					width: 1px;
					border-radius: 50%; } } }
		.list__item {
			position: relative;
			padding-left: 80px;
			&:before {
				content: '';
				position: absolute;
				width: 2px;
				height: 100%;
				background-color: #696969;
				top: 0;
				left: 60px; }
			p {
				text-transform: uppercase;
				padding: 0;
				font-family: rl;
				font-size: 18px;
				line-height: 1.5; }
			span {
				border: none;
				background-size: 50px; }
			p:last-child {
				color: #eaeaee;
				margin-top: 20px;
				font-size: 14px; } } }
	&__manager {
		width: 480px;
		margin-left: 150px;
		padding-top: 40px;
		> span {
			display: inline-block;
			position: relative;
			padding: 30px 0;
			font-family: rl;
			color: $accent;
			line-height: 1.7;
			border-top: 1px solid $accent;
			border-bottom: 1px solid $accent;
			&:after {
				content: '\201C';
				position: absolute;
				height: 40px;
				width: 40px;
				top: -20px;
				left: 0;
				background-color: #fff;
				color: $accent;
				font-family: bold;
				font-size: 100px;
				line-height: 80px; } }
		button {
			margin-top: 30px;
			padding: 18px 60px; }
		.list__item {
			color: $accent;
			padding-left: 160px;
			font-size: 18px;
			min-height: 140px;
			span {
				border: none;
				width: 140px;
				height: 140px;
				background-size: cover; }
			h4 {
				font-size: 18px; }
			p {
				font-size: 18px; } } } }



