  src: url("../fonts/RobotoRegular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "bold";
  src: url("../fonts/RobotoBold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "rl";
  src: url("../fonts/RobotoLight.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "rm";
  src: url("../fonts/RobotoMedium.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

body {
  font-family: 'rt', sans-serif;
  background: #FFF;
  line-height: 1.4;
  overflow-x: hidden;
  width: 100%;
  font-weight: 300;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  display: inline-block;
  height: auto;
}

h2 {
  font-family: bold, sans-serif;
  font-size: 24px;
  line-height: 1.1;
  text-transform: uppercase;
}

header, footer, section {
  position: relative;
  z-index: 1;
  display: flex;
  flex-wrap: wrap;
  max-width: 1280px;
  margin: 0 auto;
}

.flex {
  display: flex;
  flex-wrap: wrap;
}

.btn {
  display: inline-block;
  padding: 10px 20px;
  text-transform: uppercase;
  font-size: 12px;
  background-color: #333333;
  box-shadow: 0px 3px 6.65px 0.35px rgba(0, 0, 1, 0.09);
  color: #fff;
  transition: color, background-color .25s ease-in-out;
}

.btn:hover {
  color: #333333;
  background-color: #fff;
}

.btn:active {
  color: #333333;
  background-color: #cccccc;
}

.btn__sky {
  background-color: #28a7e4;
  padding: 18px 0;
}

.btn__sky:hover {
  background-color: #fff;
}

.btn__sky:active {
  background-color: #cccccc;
}

.btn__grey {
  background-color: #4a5666;
  padding: 15px 31px;
}

.btn__grey:hover {
  color: #fff;
  background-color: #79889d;
}

.btn__grey:active {
  color: #fff;
  background-color: #353d48;
}

.section-title {
  margin-top: 80px;
  width: 100%;
  position: relative;
  flex-wrap: no-wrap;
  justify-content: space-between;
  color: #333;
}

.section-title h2 {
  font-size: 22px;
  line-height: 1.45;
  position: relative;
  display: inline-block;
}

.section-title h2 span {
  font-family: rl;
}

.section-title__icon {
  display: inline-block;
  width: 55px;
  height: 55px;
  transform: translateY(-25%);
  border: 2px solid #e1e1e1;
  border-radius: 50%;
  background-repeat: no-repeat;
  background-size: 25px;
  background-position: center;
}

.line {
  display: inline-block;
  flex-grow: 1;
  height: 100%;
  margin: 14px 15px 0 15px;
  border-top: 1px solid #acacac;
}

.iconList li {
  padding: 0 50px 0 30px;
  position: relative;
  margin-bottom: 20px;
  color: #4a5666;
}

.iconList li:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  background-repeat: no-repeat;
  background-position: center;
}

.iconbox span {
  display: inline-block;
  margin-right: 10px;
  width: 50px;
  height: 50px;
  background-color: #e8e8e8;
  border-radius: 50%;
  background-repeat: no-repeat;
  background-size: 25px;
  background-position: center;
}

form {
  width: 100%;
}

form input, form textarea {
  width: 100%;
  padding: 10px 30px;
  background-color: transparent;
  color: #fff;
  font-size: 14px;
  border: none;
  border-bottom: 2px solid #fff;
  margin-bottom: 10px;
  outline: none;
  resize: none;
}

form input::placeholder, form textarea::placeholder {
  color: #fff;
}

form .btn__form {
  margin-top: 10px;
}

.header {
  justify-content: space-between;
  font-size: 12px;
  min-height: 120px;
}

.header-wrapper {
  position: relative;
  background-color: #4a5666;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center top;
  min-height: 400px;
}

.header-wrapper:after {
  content: '';
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  background-color: #4a5666;
  opacity: .5;
}

.header-wrapper:before {
  content: '';
  position: absolute;
  right: 0;
  bottom: 0;
  width: 570px;
  height: 570px;
  background-image: url(../img/bg-1.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.logo {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
}

.logo img {
  max-width: 190px;
}

.logo span {
  margin: 0 0 0 auto;
  color: #ffffff;
  font-family: rl;
}

nav ul {
  align-items: center;
}

nav ul li {
  display: inline-block;
  margin: 0 15px;
}

nav ul li a {
  font-weight: 400;
  text-transform: uppercase;
  color: #fff;
  display: inline-block;
  position: relative;
  padding-bottom: 3px;
}

nav ul li a::before {
  content: '';
  bottom: 0;
  width: 0;
  height: 2px;
  position: absolute;
  background-color: #aeb1b2;
  transition: width .25s ease-in-out;
}

nav ul li a:hover::before {
  width: 100%;
}

.menu-active::before {
  width: 100%;
}

.feedback {
  display: flex;
  align-items: center;
}

.feedback a:first-child {
  color: #ffffff;
  font-size: 16px;
  margin-right: 20px;
}

.feedback a:first-child strong {
  font-size: 20px;
  font-weight: 400;
}

.content {
  color: #ffffff;
  padding: 100px 0 150px 0;
}

.content-title {
  width: 35%;
}

.content-title h1 {
  text-transform: uppercase;
  font-size: 48px;
  position: relative;
  padding-bottom: 20px;
}

.content-title h1::before {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  height: 2px;
  width: 20%;
  background-color: #75777d;
}

.content-title p {
  margin: 20px 0;
  font-family: rl;
}

.content-title button {
  padding: 20px 30px;
}

.content-title a {
  color: #ffffff;
  transition: color .25s ease;
}

.content-title a:hover {
  color: #848b94;
}

.content-list {
  min-width: 250px;
  margin: 16px 0 0 100px;
}

.list__item {
  width: 100%;
  position: relative;
  padding-left: 75px;
  min-height: 55px;
  margin-bottom: 30px;
  color: #fff;
}

.list__item span {
  position: absolute;
  display: inline-block;
  left: 0;
  top: 0;
  width: 55px;
  height: 55px;
  border: 3px solid #385991;
  border-radius: 50%;
  background-repeat: no-repeat;
  background-size: 25px;
  background-position: center;
}

.list__item h4 {
  text-transform: uppercase;
  font-size: 12px;
  padding-top: 7px;
}

.list__item p {
  font-size: 12px;
  font-family: rl;
  margin-top: 7px;
}

.problem__cart {
  margin: 60px 0;
  color: #333;
}

.cart {
  width: 100%;
  justify-content: space-around;
}

.cart__item {
  width: 33%;
  min-height: 500px;
  text-align: center;
  overflow: hidden;
  position: relative;
}

.cart__item:first-child {
  border-top: 10px solid #e8e8e8;
}

.cart__item:last-child {
  border-top: 10px solid #e8e8e8;
}

.cart__item-box {
  position: relative;
  width: 100%;
  height: 440px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.cart__item-box-center:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #e0e633;
  opacity: .1;
}

.cart__item:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: translateY(-100%);
  background-color: #4a5666;
  transition: transform .25s ease;
}

.cart__item:hover {
  color: #fff;
  border-top: 10px solid #4a5666;
}

.cart__item:hover:after {
  transform: translateY(0);
}

.cart__img {
  display: inline-block;
  position: absolute;
  z-index: 2;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 110px;
  height: 110px;
  border: 1px solid #dcdcdc;
  border-radius: 50%;
  background-repeat: no-repeat;
  background-size: 50px;
  background-position: center;
}

.cart h4 {
  margin: 50px 0;
  text-transform: uppercase;
  font-size: 18px;
  position: relative;
  z-index: 2;
}

.cart h4 span {
  font-family: rl;
}

.fact {
  padding: 40px 0 60px 0;
  width: 100%;
  text-align: center;
  color: #fff;
  background-repeat: no-repeat;
  background-origin: center;
  background-size: cover;
}

.fact h3 {
  margin-bottom: 20px;
  text-transform: uppercase;
  letter-spacing: 4px;
  font-size: 26px;
}

.fact p {
  font-family: rl;
  font-size: 24px;
}

.method__icons {
  margin-top: 40px;
}

.listimg {
  width: 100%;
  padding: 75px 0 30px 0;
  flex-wrap: nowrap;
  align-items: flex-start;
  color: #333;
}

.listimg__left {
  min-width: 440px;
  margin-right: 80px;
  border-bottom: 5px solid #e8e8e8;
}

.listimg__left a {
  display: inline-block;
  margin-bottom: 20px;
  width: 100%;
  height: 200px;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  cursor: pointer;
  color: #333;
  filter: saturate(0);
  transition: all .25s ease;
}

.listimg__left a:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  opacity: .4;
  transition: all .25s ease;
}

.listimg__left a:first-child {
  background-position: left -70px;
  background-size: 160%;
}

.listimg__left a:focus {
  color: #fff;
  filter: saturate(1);
}

.listimg__left a:focus:after {
  background-color: #000;
  opacity: .4;
}

.listimg__left a:hover:after {
  box-shadow: inset 0px 0px 30px 20px rgba(0, 0, 0, 0.7);
}

.listimg__left a h4 {
  margin: 70px 0 0 20px;
  text-transform: uppercase;
  font-size: 26px;
  position: relative;
  z-index: 1;
}

.listimg__right {
  width: 100%;
}

.listimg__right h3 {
  font-size: 30px;
  text-transform: uppercase;
  line-height: 1.3;
  margin-bottom: 30px;
}

.listimg__right p {
  font-size: 25px;
  color: #4a5666;
  margin-bottom: 30px;
}

.listimg__right-item li:before {
  top: 2px;
  left: 0;
  width: 18px;
  height: 18px;
  border: 3px solid #385991;
  border-radius: 50%;
}

.list__active {
  color: #fff !important;
  filter: saturate(1) !important;
}

.list__active:after {
  background-color: #000 !important;
  opacity: .4 !important;
}

.list__active:before {
  content: '';
  position: absolute;
  height: 0;
  width: 0;
  top: 30px;
  right: -60px;
  border-top: 70px solid transparent;
  border-left: 50px solid #e8e8e8;
  border-bottom: 70px solid transparent;
}

.listimg__slider {
  width: 100%;
  overflow-x: hidden;
}

