}

.stage__slide-right h3 {
  font-family: bold;
  color: #333;
  text-transform: uppercase;
  font-size: 32px;
}

.stage__slide-right-list {
  margin-top: 30px;
}

.stage__slide-right-list li:before {
  width: 20px;
  height: 20px;
  background-image: url(../img/svgo/tick-box.svg);
