}

.stage__nav {
  position: absolute;
  display: inline-block;
  width: 80px;
  height: 67px;
  font-size: 35px;
  line-height: 67px;
  bottom: 50px;
  color: #757575;
  left: 52%;
  background-color: #dfdfdf;
  transition: background-color .25s ease, color .25s ease;
}

.stage__nav:hover {
  background-color: #4a5666;
  color: #fff;
}

.stage__nav-next {
  left: calc(52% + 80px);
}

.stage__nav-text {
  position: absolute;
  height: 67px;
  width: 120px;
  padding-left: 10px;
  font-size: 18px;
  font-family: bold;
  line-height: 73px;
  left: calc(52% + 160px);
  bottom: 50px;
  color: #333;
  text-transform: uppercase;
  background-color: #dfdfdf;
}

.profit__content {
  width: 100%;
  padding: 80px 0 40px 0;
  justify-content: space-between;
}

.profit__content-left, .profit__content-right {
  width: 300px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 350px;
}

.profit__content-left span, .profit__content-right span {
  position: relative;
  width: 100%;
  display: inline-block;
  background-color: #e8e8e8;
  text-align: right;
  padding: 20px 30px 20px 60px;
  color: #333;
}

.profit__content-left span:before, .profit__content-right span:before {
  content: '';
  position: absolute;
